import View from './View.js';
import icons from 'url:../../../public/icons/icons.svg';

class HorizontalMasonryGalleryView extends View {
  _parentElement = '';

  _generateMarkup() {
    const id = window.location.hash.slice(1);
    // const date = new Date(
    //   this._data.date.toLocaleString('en-GB', { year: 'numeric' })
    // );

    return `
        <div class="repeat card h-m-card ${
          this._data.id === id ? 'h-m-card--active' : ''
        }" style="width:${
      (this._data.width.px * 220) / this._data.height.px
    }px;flex-grow:${
      (this._data.width.px * 220) / this._data.height.px
    }" data-toggle-modal="popover">
          <a href="#${this._data.id}">
              <i style="padding-bottom:${
                (this._data.height.px / this._data.width.px) * 100
              }%"></i>
              <img class="horizontal-gallery-img" src="images/artworks/${
                this._data.image
              }" crossorigin="anonymous" alt="${
      this._data.title
    }" class="gallery-img">
    </a>
    <button class="card__btn-round card__btn--bookmark">
          <svg class="">
          <use class="card__icon-bookmark" href="${icons}#icon-bookmark${
      this._data.bookmarked ? '-fill' : ''
    }"</use>
          </svg>
        </button>
        </div>
        <title>${this._data.title}</title>
          `;
  }

  // <div class="card__details">
  // <ul>
  // <li class="card__artwork-title">${this._data.title}</li>
  // <li class="card__artwork-dimensions">${this._data.dims.height.px} x ${
  //   this._data.dims.width.px
  // }cm</li>
  // </ul>
  // </div>
}

export default new HorizontalMasonryGalleryView();
