import View from './View.js';

class ModalPopupView extends View {
  _parentElement = document.querySelector('.card');

  _toggleModal() {}

  _addHandlerRender() {
    this._parentElement.addEventListener('hover', this._toggleModal.bind(this));
  }

  _generateMarkup() {
    return `
      <div class="hover-modal">
        <div class="hover-modal__info-container">
          <dl class="hover-modal__details-dl">
            <dd>
              <h5 class="aside__title">"${this._data.title}"</h5>
            </dd>
            <dd>
              <time class="aside__date" datetime=${this._data.date}>${date}</time>
            </dd>
            <dd class="aside__medium">${this._data.medium}s</dd>
            <dd class="aside__dimensions">${this._data.dims.height} x ${this._data.dims.width}cm</dd>
            <dd class="aside__sales-info">Â£${this._data.price}</dd>
            <dd class="aside__location">Location: ${this._data.location}</dd>
          </dl>
        </div>
      </div>
    `;
  }
}

export default new ModalPopupView();
