import { API_URL, RES_PER_PAGE, URL_PATH_1, URL_PATH_2 } from './config.js';
import { getJSON } from './helpers.js';

export const state = {
  artworks: [],
  artwork: {},
  search: {
    query: '',
    results: [],
    page: 1,
    resultsPerPage: RES_PER_PAGE,
    filterFields: [],
  },
  bookmarks: [],
  galleryMode: [],
  theme: '',
  texts: [],
};

export const loadArtworks = async function () {
  try {
    const data = await getJSON(API_URL + 'gallery/');

    state.artworks = data.data.artworks.map((artwork) => {
      return {
        id: artwork.id,
        image: artwork.imageCover,
        title: artwork.title,
        medium: artwork.medium,
        date: artwork.date,
        dims: artwork.dimensions,
        height: artwork.dimensions.height,
        width: artwork.dimensions.width,
        price: artwork.sales.price,
        priceRange: artwork.sales.priceRange,
        categories: artwork.categories,
        tags: artwork.tags,
      };
    });
    console.log(state.artworks);
  } catch (err) {
    console.error(`${err} ðŸ’¥ðŸ’¥ðŸ’¥`);
    throw err;
  }
};

