export const getSearchResultsPage = function (page = state.search.page) {
  state.search.page = page;

  const start = (page - 1) * state.search.resultsPerPage;
  const end = page * state.search.resultsPerPage;

  return state.search.results.slice(start, end);
};

export const loadArtwork = async function (id) {
  try {
    const data = await getJSON(`${API_URL}gallery/artwork/${id}`);
    // console.log(data);

    const { artworks } = data.data;
    state.artwork = {
      id: artworks.id,
      image: artworks.imageCover,
      title: artworks.title,
      medium: artworks.medium,
      date: artworks.date,
      dims: artworks.dimensions,
      height: artworks.dimensions.height,
      width: artworks.dimensions.width,
      price: artworks.sales.price,
      priceRange: artworks.sales.priceRange,
      categories: artworks.categories,
      tags: artworks.tags,
      description: artworks.description,
      location: artworks.location,
    };

    if (state.bookmarks.some((bookmark) => bookmark.id === id))
      state.artwork.bookmarked = true;
    else state.artwork.bookmarked = false;
  } catch (err) {
    console.error(`${err} ðŸ’¥ðŸ’¥ðŸ’¥`);
    throw err;
  }
};

const persistBookmarks = function () {
  localStorage.setItem('bookmarks', JSON.stringify(state.bookmarks));
};

export const addBookmark = function (artwork) {
  // When adding get entire data
  // Add bookmark
  state.bookmarks.push(artwork);

  // Mark current artwork as bookmark
  if (
    artwork.id === state.artwork.id ||
    artwork.id === state.search.results.id
  ) {
    state.artwork.bookmarked = true;
    state.search.results.bookmarked = true;
  }
  persistBookmarks();
};

export const deleteBookmark = function (id) {
  // Delete bookmark
  // When deleting only get id
  const index = state.bookmarks.findIndex((el) => el.id === id);
  state.bookmarks.splice(index, 1);

  // Mark current artwork as NOT bookmarked
  if (id === state.artwork.id) state.artwork.bookmarked = false;
  if (id === state.search.results.id) state.search.results.bookmarked = false;

  persistBookmarks();
};

