export const loadFilterCategories = async function (...filters) {
  try {
    const data = await getJSON(`${API_URL}gallery/${filters}`);

    state.search.filterFields = data.data.artworks.map((artwork) => {
      return {
        medium: artwork.medium[0],
        date: artwork.date,
        dimensions: artwork.dimensions,
        price: artwork.sales.price,
        priceRange: artwork.sales.priceRange,
        categories: artwork.categories,
        tags: artwork.tags,
        location: artwork.location,
      };
    });
  } catch (err) {
    console.error(`${err} ðŸ’¥ðŸ’¥ðŸ’¥`);
    throw err;
  }
};

