export const setTheme = function (theme) {
  if (theme === 'dark') localStorage.setItem('data-theme', 'dark');
  if (theme === null || theme === 'light')
    localStorage.setItem('data-theme', 'light');
  // state.theme = theme;

  // updateTheme();
};

const themeInit = function () {
  const storage = localStorage.getItem('data-theme');
  if (storage) state.theme = storage;
};
themeInit();

export const loadTexts = async function () {
  try {
    const data = await getJSON(API_URL + URL_PATH_2);

    state.texts = data.data.texts.map((text) => {
      return {
        id: text.id,
        heading: text.heading,
        subHeading: text.subHeading,
        content: text.content,
        dateWritten: text.dateWritten,
        version: text.version,
        categories: text.categories,
        rating: text.rating,
        artwork: text.artwork,
        user: text.user,
      };
    });
  } catch (err) {
    console.log(err);
  }
};
