import View from './View.js';
import icons from 'url:../../../public/icons/icons.svg';

class BookmarksView extends View {
  _parentElement = document.querySelector('.gallery-container');
  _errorMessage =
    'No bookmarks yet. Choose your favourite artworks and bookmark them ;)';

  addHandlerRender(handler) {
    window.addEventListener('load', handler);
  }

  _generateMarkup() {
    const date = new Date(this._data.date);
    const year = date.toLocaleString('en-GB', { year: 'numeric' });

    const id = window.location.hash.slice(1);
    // ${this._data.render(cardsHoverModalView)}

    return `
      <div class="card ${this._data.id === id ? 'card--active' : ''}">
        <div class="card__header"></div>
        <a class="card__image-container preview__link ${
          this._data.id === id ? 'preview__link--active' : ''
        }" href="#${this._data.id}">
        <img src="images/artworks/${
          this._data.image
        }" crossorigin="anonymous" alt="${
      this._data.title
    }" class="gallery-img">
        <button class="card__btn-round card__btn--bookmark">
          <svg class="">
          <use class="card__icon-bookmark" href="${icons}#icon-bookmark${
      this._data.bookmarked ? '-fill' : ''
    }"</use>
          </svg>
        </button>
      </a>
        <div class="card__space"></div>
        <div class="card__details">
          <ul>
            <li class="card__artwork-title">${this._data.title}</li>
            <li class="card__artwork-date">${year}</li>
            <li class="card__artwork-medium">${this._data.medium[0]}</li>
            <li class="card__artwork-dimensions">${
              this._data.dims.height.px
            } x ${this._data.dims.width.px}cm</li>
          </ul>
        </div>
        <div class="card__footer"></div>
      </div>
    `;
  }
}

export default new BookmarksView();
