const controlGallery = async function () {
  try {
    // Disable to access artworks with inputting #id
    // const id = window.location.hash.slice(1);
    // console.log(id);

    // if (!id) return;
    galleryView.renderSpinner();

    // 1) galleryModeInit() is called in model
    //FIXME horizontalMasonryGallery cannot calculate dimensions until data is loaded. Temp fix = set gridCardsGallery to default, but unforunately I wanted horizontalMasonry as default!
    // window.addEventListener(
    //   'loadeddata',
    galleryView.setGalleryMode(model.state.galleryMode);
    // );
    // galleryView.initGalleryMode(model.state.galleryMode);

    // Update results view to mark selected search results
    // galleryView.update(model.getSearchResultsPage());

    // loading Gallery
    await model.loadArtworks();
    // await model.loadSearchResults();

    // Rendering Gallery
    // galleryView.render(model.state.search.results);
    galleryView.render(model.state.artworks);
  } catch (err) {
    galleryView.renderError();
    console.log(err);
  }
};

const controlGalleryMode = async function (mode) {
  try {
    // 1) galleryModeInit() is called in model
    galleryView.renderSpinner();
    model.updateGalleryMode(mode);
    galleryView.setGalleryMode(model.state.galleryMode);
    // await model.loadSearchResults();
    // galleryView.render(model.state.search.results);
    await model.loadArtworks();
    galleryView.render(model.state.artworks);
  } catch (err) {
    console.log(err);
  }
};

const controlSearchResults = async function () {
  try {
    // resultsView.renderSpinner();
    galleryView.renderSpinner();
    // console.log(resultsView);
    // 1) Get search query
    const query = searchView.getQuery();
    if (!query) return;

    // 2) load search results
    // await model.loadSearchResults();
    await model.loadSearchResults(query);

    // 3) Render results
    // console.log(model.state.search.results);
    // resultsView.render(model.state.search.results);
    // resultsView.render(model.getSearchResultsPage());
    galleryView.render(model.getSearchResultsPage());
    // controlPanelView.render(model.getSearchResultsPage);
    // controlPanelView.render(model.getSearchResultsPage());
    // galleryView.render()
    //
    // 4) Render initial pagination buttons
    paginationView.render(model.state.search);
  } catch (err) {
    console.log(err);
  }
};

const controlPagination = function (goToPage) {
  // 1) Render NEW results
  galleryView.render(model.getSearchResultsPage(goToPage));

  // 2) Render NEW pagination buttons
  paginationView.render(model.state.search);
};

const controlAsideModal = async function () {
  try {
    const id = window.location.hash.slice(1);
    console.log(id);

    if (!id) return;
    asideModalView.renderSpinner();

    await model.loadArtwork(id);
    // await model.loadArtworks();

    // asideView.render(model.state.artworks);
    asideModalView.render(model.state.artwork);
    asideModalView.modalZoom();
    // asideModalView.update(model.state.artwork);
  } catch (err) {
    asideModalView.renderError();
  }
};

const controlAddBookmark = function () {
  // 1) Add or remove bookmark
  if (
    !model.state.artwork.bookmarked ||
    !model.state.search.results.bookmarked
    // !model.state.artworks.bookmarked
  ) {
    model.addBookmark(model.state.artwork);
    model.addBookmark(model.state.search.results);
    // model.addBookmark(model.state.artworks);
  } else {
    model.deleteBookmark(model.state.artwork.id);
    model.deleteBookmark(model.state.search.results.id);
    // model.deleteBookmark(model.state.artworks.id);
  }

  // 2) Update aside / gallery view
  asideModalView.update(model.state.artwork);
  galleryView.update(model.state.search.results);
  // galleryView.update(model.state.artworks);

  // bookmarksView.render(model.state.search.results);

  // 3) Render bookmarks
  // galleryView.render(model.state.artwork);
  // bookmarksView.render(model.state.bookmarks);
};

const controlBookmarks = function () {
  // bookmarksView.render(model.state.bookmarks);
  // searchView.render(model.state.bookmarks);
  galleryView.render(model.state.bookmarks);
};

const controlPanel = async function () {
  try {
  } catch (err) {
    console.log(err);
  }
};

const controlSettingsModal = async function () {
  settingsModalView.render();
};

const controlFilters = async function (filters) {
  try {
    await model.loadFilterCategories();
    filtersView.render(model.state.search.filterFields);
    // console.log(model.state.search.filterFields, 'ðŸ˜‚');
  } catch (err) {
    console.log(err);
  }
};

const controlArtistStatement = async function () {
  try {
    artistStatementView.render(model.state.artistStatement);
  } catch (err) {
    console.log(err);
  }
};

