const init = function () {
  bookmarksView.addHandlerRender(controlBookmarks);
  galleryView.addHandlerRender(controlGallery);
  galleryView.addHandlerClick(controlGalleryMode);
  controlPanelView.addHandlerRender(controlPanel);
  filtersView.addHandlerRender(controlFilters);
  searchView.addHandlerSearch(controlSearchResults);
  // galleryView.addHandlerRender(controlSearchResults);
  paginationView.addHandlerClick(controlPagination);
  galleryView.addHandlerAddBookmark(controlAddBookmark);
  // modalPopupView.addHandlerRender()
  // cardsHoverModalView.add
  // asideView must be called AFTER galleryView and paginationView
  asideModalView.addHandlerRender(controlAsideModal);
  asideModalView.addHandlerAddBookmark(controlAddBookmark);
  galleryView.addHandlerAddBookmark(controlAddBookmark);
  // model.restoreBookmarks();
  artistStatementView.addHandlerRender(controlArtistStatement);
};
init();

