import View from './View.js';
import { GALLERY_MODES } from '../config.js';
import gridCardsGalleryView from './gridCardsGalleryView.js';
import horizontalMasonryGalleryView from './horizontalMasonryGalleryView.js';
import bookmarksView from './bookmarksView.js';

class GalleryView extends View {
  _parentElement = document.querySelector('.gallery-container');
  _errorMessage = 'That artwork could not be found. Please try another one!';
  _message = '';

  _gallerySection = document.querySelector('#section-gallery');
  _horizontalMasonryBtn = document.querySelector('.hmc__btn');
  _gridCardsBtn = document.querySelector('.gc__btn');
  _bookmarksBtn = document.querySelector('.bookmarks__btn');

  _card = document.querySelector('.card');
  _btnToggleCardInfo = document.querySelector('.btn--toggle__card-info');
  // _cardInfo = document.querySelector('.card__details');

  constructor() {
    super();
    this._addHandlerToggleCardInfo();
  }

  addHandlerRender(handler) {
    window.addEventListener('load', handler);
  }

  date() {
    const date = new Date(this._data.date);
    const year = date.toLocaleString('en-GB', { year: 'numeric' });
    return year;
  }

  /**
   *
   * @param {Object} mode The 'gallery mode' (e.g. 'grid-cards-gallery', 'horizonstal-masonry-gallery') to be rendered and set in localStorage.
   * @todo Refactor and reduce repeated code; consider exporting functionality inside addHandlerClick() into another method with the 'this' keyword bound to it;
   */
  setGalleryMode(mode) {
    if (mode) {
      this._gallerySection.setAttribute('data-gallery-mode', mode);
    } else {
      this._gallerySection.getAttribute('data-gallery-mode');
    }
  }

  addHandlerClick(handler) {
    if (this._horizontalMasonryBtn) {
      this._horizontalMasonryBtn.addEventListener('click', (e) => {
        this.setGalleryMode(GALLERY_MODES[0]);
        const { galleryMode } = this._gallerySection.dataset;
        handler(galleryMode);
      });
    }
    if (this._gridCardsBtn) {
      this._gridCardsBtn.addEventListener('click', (e) => {
        this.setGalleryMode(GALLERY_MODES[1]);
        const { galleryMode } = this._gallerySection.dataset;
        handler(galleryMode);
      });
    }
    if (this._bookmarksBtn) {
      this._bookmarksBtn.addEventListener('click', () => {
        this.setGalleryMode(GALLERY_MODES[2]);
        const { galleryMode } = this._gallerySection.dataset;
        handler(galleryMode);
      });
    }
  }

  _generateMarkup() {
    const currentMode = this._gallerySection.getAttribute('data-gallery-mode');
    if (currentMode === GALLERY_MODES[0]) {
      this._gridCardsBtn.classList.remove('cp__btn--active');
      this._bookmarksBtn.classList.remove('cp__btn--active');
      this._horizontalMasonryBtn.classList.add('cp__btn--active');
      return this._data
        .map((cards) => horizontalMasonryGalleryView.render(cards, false))
        .join('');
    }
    if (currentMode === GALLERY_MODES[1]) {
      this._horizontalMasonryBtn.classList.remove('cp__btn--active');
      this._bookmarksBtn.classList.remove('cp__btn--active');
      this._gridCardsBtn.classList.add('cp__btn--active');
      return this._data
        .map((cards) => gridCardsGalleryView.render(cards, false))
        .join('');
    }
    if (currentMode === GALLERY_MODES[2]) {
      this._gridCardsBtn.classList.remove('cp__btn--active');
      this._horizontalMasonryBtn.classList.remove('cp__btn--active');
      this._bookmarksBtn.classList.add('cp__btn--active');
      return this._data.bookmarks
        .map((cards) => bookmarksView.render(cards, false))
        .join('');
    }
  }

  /**
   * @todo Finish implementation
   */
  _addHandlerToggleCardInfo() {
    this._btnToggleCardInfo.addEventListener(
      'click',
      this.toggleCardInfo.bind(this)
    );
  }
  toggleCardInfo() {
    this._cardInfo.classList.toggle('hidden');
  }

  addHandlerAddBookmark(handler) {
    this._parentElement.addEventListener('click', function (e) {
      const btn = e.target.closest('.card__btn--bookmark');
      if (!btn) return;
      handler();
    });
  }
}
export default new GalleryView();
