import View from './View.js';

class controlPanelView extends View {
  // _parentElement = document.querySelector('.control-panel');
  _controlPanel = document.querySelector('.control-panel');
  _cogBtn = document.querySelector('.cog-btn');
  _galleryWindow = document.querySelector('.gallery-container');
  _galleryWindow = document.querySelector('.gallery-window');
  _gallerySettings = document.querySelector('.gallery-settings');
  _filterMenu = document.querySelector('.filter-menu__container');
  _galleryControlPanel = document.querySelector('.gallery-control-panel');
  _btnToggleControlPanel = document.querySelector(
    '.btn--toggle__control-panel'
  );
  // _btnToggleBoxB = document.querySelector('.btn--toggle__box-B');

  _btnToggleFilterMenu = document.querySelector('.btn--toggle__filter-menu');
  // _btnToggleGalleryWindow = document.querySelector(
  //   '.btn--toggle__gallery-window'
  // );

  constructor() {
    super();
    this._addHandlerToggle();
  }

  addHandlerRender(handler) {
    window.addEventListener('load', handler);
  }

  toggleCP() {
    this._controlPanel.classList.toggle('hidden');
    this._filterMenu.classList.toggle('hidden');
    this._galleryControlPanel.classList.toggle('hidden');
  }
  toggleGW() {
    this._galleryWindow.classList.toggle('.collapse-Y');
    this._controlPanel.classList.toggle('hidden');
  }
  toggleFM() {
    this._filterMenu.classList.toggle('hidden');
  }

  _addHandlerToggle() {
    this._btnToggleControlPanel.addEventListener(
      'click',
      this.toggleCP.bind(this)
    );
    this._btnToggleControlPanel.addEventListener(
      'click',
      this.toggleCP.bind(this)
    );
    // this._btnToggleGalleryWindow.addEventListener(
    //   'click',
    //   this.toggleGW.bind(this)
    // );
    this._btnToggleFilterMenu.addEventListener(
      'click',
      this.toggleFM.bind(this)
    );
  }

  _generateMarkup() {}
}
export default new controlPanelView();
