import View from './View.js';

//TODO
class ResultsView extends View {
  _parentElement = document.querySelector('.aside');
  _errorMessage = 'No artworks found for your query! Please try again ;)';
  _message = '';
  // _parentElement = document.querySelector('.results');

  _generateMarkup() {
    // console.log(this._data);
    return this._data.map(this._generateMarkupPreview).join('');
  }

  _generateMarkupPreview(result) {
    return `
      <div class="aside__slide">
        <img src="images/artworks/${result.image}" alt="" class="hover-shadow preview" />
      </div>
      <div class="aside__spacer"></div>
      <div class="aside__text-container">
        <span class="aside__title">"${result.title}"</span>
        <span class="aside__date">${result.date}</span>
        <span class="aside__medium">${result.medium}s</span>
        <span class="aside__dimensions">${result.dims.height}x${result.dims.width}</span>
        <span class="aside__sales-info">${result.price}</span>
      </div>
      <span class="aside__description"
      >${result.description}</span>
    `;
  }
}

export default new ResultsView();
